<template>
  <div class="add-scene">
    <el-form
      :model="menuForm"
      ref="menuForm"
      label-width="120px"
      class="demo-ruleForm"
    >
      <el-form-item
        v-if="$route.name !== 'addKeyword' && $route.name !== 'editKeyword'"
        label="菜单名称"
        prop="name"
        :rules="[{ required: true, message: '请输入名称', target: 'blur' }]"
      >
        <el-input v-model="menuForm.name" clearable placeholder="请输入名称" />
      </el-form-item>
      <el-form-item
        v-if="$route.name === 'addKeyword' || $route.name === 'editKeyword'"
        label="关键词"
        prop="keyword"
      >
        <el-input
          v-model="menuForm.keyword"
          clearable
          placeholder="请输入关键词"
        />
      </el-form-item>
      <el-form-item
        v-if="$route.name !== 'addKeyword' && $route.name !== 'editKeyword'"
        label="类型"
        prop="type"
        :rules="[{ required: true, message: '请选择类型', target: 'change' }]"
      >
        <el-select
          v-model="menuForm.type"
          placeholder="请选择类型"
          @change="setMenu"
        >
          <el-option
            v-for="item in typeList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item
        v-if="menuForm.type === 'view' || menuForm.type === 'miniprogram'"
        label="url"
        prop="url"
      >
        <el-input v-model="menuForm.url" clearable placeholder="请输入url" />
      </el-form-item>
      <el-form-item
        v-if="
          menuForm.type !== 'view' &&
            menuForm.type !== 'miniprogram' &&
            menuForm.type !== 'media_id' &&
            menuForm.type !== 'view_limited' &&
            $route.name !== 'addKeyword' &&
            $route.name !== 'editKeyword'
        "
        label="key"
        prop="key"
        :rules="[{ required: true, message: '请输入key', target: 'blur' }]"
      >
        <el-input v-model="menuForm.key" clearable placeholder="请输入key" />
      </el-form-item>
      <el-form-item
        v-if="
          menuForm.type === 'miniprogram' &&
            $route.name !== 'addKeyword' &&
            $route.name !== 'editKeyword'
        "
        label="页面路径"
        prop="pagePath"
        :rules="[
          { required: true, message: '请输入小程序的页面路径', target: 'blur' }
        ]"
      >
        <el-input
          v-model="menuForm.pagePath"
          clearable
          placeholder="请输入小程序的页面路径"
        />
      </el-form-item>
      <el-form-item
        v-if="
          menuForm.type === 'miniprogram' &&
            $route.name !== 'addKeyword' &&
            $route.name !== 'editKeyword'
        "
        label="AppId"
        prop="appId"
        :rules="[
          { required: true, message: '请输入小程序的AppId', target: 'blur' }
        ]"
      >
        <el-input
          v-model="menuForm.appId"
          clearable
          placeholder="请输入小程序的AppId"
        />
      </el-form-item>
      <el-form-item
        v-if="
          menuForm.type === 'media_id' ||
            menuForm.type === 'view_limited' ||
            $route.name === 'addKeyword' ||
            $route.name === 'editKeyword' ||
            menuForm.type === 'click'
        "
        label="返回内容格式"
        prop="content_type"
        :rules="[{ required: true, message: '请选择格式', target: 'change' }]"
      >
        <el-select v-model="menuForm.content_type" placeholder="请选择格式">
          <el-option
            v-for="item in content_type_list"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item
        v-if="
          (menuForm.type === 'media_id' || menuForm.type === 'view_limited') &&
            $route.name !== 'addKeyword' &&
            $route.name !== 'editKeyword'
        "
        label="素材Id"
        prop="media_id"
      >
        <el-select v-model="menuForm.media_id" placeholder="请选择素材">
          <el-option
            v-for="item in mediaList"
            :key="item.media_id"
            :label="item.name"
            :value="item.media_id"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item
        v-if="
          $route.name === 'addKeyword' ||
            $route.name === 'editKeyword' ||
            menuForm.type === 'click'
        "
        label="返回数据"
        prop="content"
        :rules="[{ required: true, message: '请输入返回数据', target: 'blur' }]"
      >
        <el-input
          v-if="menuForm.content_type === 'text'"
          v-model="menuForm.content"
          type="textarea"
          style="width: 300px"
          clearable
          placeholder="请输入返回数据"
        />
        <el-select
          v-if="menuForm.content_type !== 'text'"
          v-model="menuForm.content"
          placeholder="请选择素材"
        >
          <el-option
            v-for="item in mediaList"
            :key="item.media_id"
            :label="item.name"
            :value="item.media_id"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="setUp">
        <el-button type="primary" @click="submitForm('menuForm')">
          {{ btnText }}
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {
  createMenu,
  getMedia,
  getMenuDetail,
  putMenuDetail,
  createKeyword,
  getKeywordDetail,
  editKeyword
} from '@/api/officialAccount'
export default {
  data() {
    return {
      btnText: '确认新增',
      menuForm: {
        name: '',
        type: '',
        url: '',
        key: '',
        keyword: '',
        media_id: '',
        pagePath: '',
        appId: '',
        content_type: '',
        content: ''
      },
      mediaList: [],
      typeList: [
        { value: 'view', label: '跳转url' },
        { value: 'click', label: '点击推事件' },
        { value: 'scancode_push', label: '扫码推事件' },
        {
          value: 'scancode_waitmsg',
          label: '扫码推事件且弹出“消息接收中”提示框'
        },
        { value: 'pic_sysphoto', label: '弹出系统拍照发图' },
        { value: 'pic_photo_or_album', label: '弹出拍照或者相册发图' },
        { value: 'pic_weixin', label: ' 弹出微信相册发图器' },
        { value: 'location_select', label: ' 弹出地理位置选择器' },
        { value: 'media_id', label: ' 下发消息（除文本消息）' },
        // { value: 'view_limited', label: ' 下发图文消息' },
        { value: 'miniprogram', label: ' 打开小程序' }
      ],
      content_type_list: [
        { value: 'text', label: '文本' },
        { value: 'news', label: '图文' },
        { value: 'image', label: '图片' },
        { value: 'voice', label: '语音' },
        { value: 'video', label: '视频' }
      ]
    }
  },
  watch: {
    'menuForm.content_type'(value) {
      if (
        (value === 'image' ||
          value === 'video' ||
          value === 'voice' ||
          value === 'news') &&
        (this.menuForm.type ||
          this.$route.name === 'addKeyword' ||
          this.$route.name === 'editKeyword')
      ) {
        this.getMedia()
      }
    }
  },
  created() {
    if (this.$route.name === 'editMenu' || this.$route.name === 'editKeyword') {
      if (this.$route.name === 'editMenu') this.getMenuDetail()
      else this.getKeywordDetail()
      this.btnText = '确认编辑'
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.$route.name === 'addMenu' || this.$route.name === 'editMenu')
            this.createMenu()
          else this.createKeyword()
        } else {
          return false
        }
      })
    },
    setMenu() {
      this.menuForm.url = ''
      this.menuForm.key = ''
      this.menuForm.media_id = ''
      this.menuForm.pagePath = ''
      this.menuForm.appId = ''
      this.menuForm.content_type = ''
      this.menuForm.content = ''
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    async getMedia() {
      const params = {
        page: 1,
        pageSize: 12,
        type: this.menuForm.content_type
      }
      const { code, data } = await getMedia(params)
      if (code === 1000) {
        this.mediaList = data.item.map(item => {
          return {
            name: item.name,
            media_id: item.media_id,
            url: item.url
          }
        })
      }
    },
    async createMenu() {
      const params = {
        name: this.menuForm.name,
        type: this.menuForm.type,
        parent_id: this.$route.query.id || undefined
      }
      if (this.menuForm.url) params.url = this.menuForm.url
      if (this.menuForm.key) params.key = this.menuForm.key
      if (this.menuForm.content_type)
        params.callback_type = this.menuForm.content_type
      if (this.menuForm.content) params.callback_data = this.menuForm.content
      if (this.menuForm.pagePath) params.pagepath = this.menuForm.pagePath
      if (this.menuForm.appId) params.appid = this.menuForm.appId
      if (this.menuForm.media_id) params.media_id = this.menuForm.media_id
      if (this.$route.params.id) {
        const { code, message } = await putMenuDetail(
          this.$route.params.id,
          params
        )
        if (code === 1000) {
          this.$message.success(message)
          this.$router.push({ name: 'officialAccount' })
        }
      } else {
        const { code, message } = await createMenu(params)
        if (code === 1000) {
          this.$message.success(message)
          this.$router.push({ name: 'officialAccount' })
        }
      }
    },
    async getMenuDetail() {
      const { code, data } = await getMenuDetail(this.$route.params.id)
      if (code === 1000) {
        this.menuForm.type = data.type
        this.menuForm.name = data.name
        this.menuForm.url = data.url
        this.menuForm.key = data.key
        this.menuForm.media_id = data.media_id
        this.menuForm.pagePath = data.pagepath
        this.menuForm.appId = data.appid
        this.menuForm.content_type = data.callback_type
        this.menuForm.content = data.callback_data
      }
    },
    async createKeyword() {
      const params = {
        keyword: this.menuForm.keyword,
        callback_type: this.menuForm.content_type,
        callback_data: this.menuForm.content
      }
      if (this.$route.name === 'addKeyword') {
        const { code, message } = await createKeyword(params)
        if (code === 1000) {
          this.$message.success(message)
          this.$router.push({ name: 'officialAccount' })
        }
      }
      if (this.$route.name === 'editKeyword') {
        const { code, message } = await editKeyword(
          this.$route.params.id,
          params
        )
        if (code === 1000) {
          this.$message.success(message)
          this.$router.push({ name: 'officialAccount' })
        }
      }
    },
    async getKeywordDetail() {
      const { code, data } = await getKeywordDetail(this.$route.params.id)
      if (code === 1000) {
        this.menuForm.keyword = data.keyword
        this.menuForm.content_type = data.callback_type
        this.menuForm.content = data.callback_data
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.add-scene {
  /deep/.el-input {
    width: 220px;
    input {
      height: 32px;
      line-height: 32px;
    }
  }
  .tip {
    margin-top: 10px;
    line-height: 16px;
  }
  .btn-style {
    border: 1px solid rgba(228, 230, 233, 1);
    border-radius: 4px;
    vertical-align: middle;
  }
}
</style>
